import React from 'react';
import { Add } from '@wix/wix-ui-icons-common';
import { CreateActionParams } from './types';

export const createAction = (params: CreateActionParams) => {
  const { action, sdk } = params;
  const { appConfig } = sdk;

  return {
    onClick: () => {
      if (action.create.mode === 'page') {
        const pageId = action.create.page.id;
        const pageToNavigate = appConfig.pages.find(
          (page) => page.id === pageId,
        );

        if (pageToNavigate) {
          if (pageToNavigate.type === 'entityPage') {
            const { navigateToEntityPage } = sdk;
            if (navigateToEntityPage) {
              navigateToEntityPage({
                entityPageId: pageId,
                item: null,
              });
            }
          }
        }
      }
    },
    label: 'Create',
    icon: <Add />,
  };
};
